#preloader.hide {
  opacity: 0;
  pointer-events: none;
}

#loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #9370DB;
  animation: spin 2s linear infinite;
}

#loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #BA55D3;
  animation: spin 3s linear infinite;
}

#loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #FF00FF;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.wrap-modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  pointer-events: none;
  transition: background .4s;
}

.wrap-modal.show {
  background: rgba(0, 0, 0, 0.3);
  pointer-events: auto;
}

.wrap-modal.show .modal {
  opacity: 1;
  transform: scale(1);
  transition-delay: .1s;
}

.wrap-modal .modal {
  position: absolute;
  top: 20%;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 500px;
  width: 80%;
  background: #fefefe;
  padding: 37px 30px;
  cursor: default;
  opacity: 0;
  transform: scale(0.85);
  transition: opacity   .3s, transform .3s;
}

.wrap-modal .modal .close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.wrap-modal .modal .close:before, .wrap-modal .modal .close:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 20px;
  background: #000;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.wrap-modal .modal .close:before {
  transform: rotate(45deg);
}

.wrap-modal .modal .close:after {
  transform: rotate(-45deg);
}

