  right: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.wrap-stng-item .close:before, .wrap-stng-item .close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 75%;
  height: 2px;
  background: white;
